.dialogue{
    /* border: 1px solid teal; */
    position: absolute;
    z-index: 100000;
   
    /* background-color: white; */
    margin-top: 325px;
    margin-left: 324px;
    width: 600px;
    height: 150px;
}
.dialogue::after{
    content: "";
    background-image: url('../../assets/interface/woodBackground.jpg');
    background-size: cover;
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   

}

.dialogue_text{
    width: 450px;
    padding: 10px;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    color: white;
    /* text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black; */
    text-shadow: 1px 1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    -1px -1px 0 #000,
    0px 1px 0 #000,
    0px -1px 0 #000,
    -1px 0px 0 #000,
    1px 0px 0 #000,
    2px 2px 0 #000,
    -2px 2px 0 #000,
    2px -2px 0 #000,
    -2px -2px 0 #000,
    0px 2px 0 #000,
    0px -2px 0 #000,
    -2px 0px 0 #000,
    2px 0px 0 #000,
    1px 2px 0 #000,
    -1px 2px 0 #000,
    1px -2px 0 #000,
    -1px -2px 0 #000,
    2px 1px 0 #000,
    -2px 1px 0 #000,
    2px -1px 0 #000,
    -2px -1px 0 #000;
    /* border: 1px solid blue; */
}

.dialogue_image{
    height: 150px;
    width: 150px;
    /* border: 5px solid #828282;
    outline: 3px solid silver; */
}

.dialogue_next{
    position: absolute;
    margin-left: 275px;
    margin-top: 110px;
    -webkit-box-shadow: 0 0 5px -1px rgba(0,0,0,0.2);
            box-shadow: 0 0 5px -1px rgba(0,0,0,0.2);
    /* border: 1px solid red; */
    background-image: url('../../assets/interface/rightArrow.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
    cursor: pointer;
}

.dialogue_next:active {
    -webkit-box-shadow: 0 0 5px -1px rgba(0,0,0,0.6);
            box-shadow: 0 0 5px -1px rgba(0,0,0,0.6);
}


